/**
 * NavBar
 * @param {boolean}  isEdit           
 * @param {function} rightContent     
 * @param {function} onLeftClick      
 * @param {boolean}  isDetail         -
 * @param {boolean}  isCollect        -
 */

import { NavBar } from 'antd-mobile';
import PropTypes from 'prop-types';
import React from 'react';
import BackArrow from './components/backIcon';
import './index.less';
const PublicNavBar = (props) => {
  return (
    <div style={{ boxShadow: '1px 1px 1px #e2e1e1' }}>
      <NavBar
        className='navbar'
        style={
          props.isDetail ? {
            width: '100vw',
            background: "rgba(255, 255, 255, 0)",
            position: 'absolute',
            top: props.display ? '-.9rem' : 0,
            left: 0,
            zIndex: 1,
            transition: '.4s',
          } : props.isCollect ? {
            position: 'fixed',
            top: 0,
            left: 0,
            zIndex: 2,
          } : {}
        }
        icon={<BackArrow type="left" />}
        onLeftClick={() => props.onLeftClick()}
        rightContent={props.edit ? <span onClick={() => props.onRightClick()}>{props.isEdit ? '' : ''}</span> : null}
      >
        {props.title}
      </NavBar>
    </div>
  )
}

PublicNavBar.propTypes = {
  edit: PropTypes.bool,
  isDetail: PropTypes.bool,
  isCollect: PropTypes.bool,
  onLeftClick: PropTypes.func,
  onRightClick: PropTypes.func,
}

PublicNavBar.defaultProps = {
  edit: false,
  isDetail: false,
  isCollect: false,
  onLeftClick: () => console.log('onLeftClickprops'),
  onRightClick: () => console.log('onRightClickprops'),
}


export default PublicNavBar
