import { Button, TextareaItem, Toast } from 'antd-mobile';
import copy from 'copy-to-clipboard';
import { createForm } from 'rc-form';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import PubicNavBar from '../../components/PublicNavBar';
import { contactApi } from '../../services/feedBack';
import { userFeedBacks } from '../../store/action/feedBack';
import ImagePicke from './components/imagePicker';
import './index.less';
const mapDispatchToProps = {
  userFeedBacks
};

@connect(({ videoDetailReducer }) => ({
  videoDetailReducer
}), mapDispatchToProps)
@createForm()
class UserFeedBack extends Component {
  constructor(props) {
    super(props);
    this.state = {
      msgadmin: '',
      wechat: '',
      qqno: '',
      potato: '',
      telno: '',
    }
  }

  componentDidMount() {
    contactApi({ type: '1' }).then(res => {
      if (res) {
        const {
          msgadmin,
          wechat,
          qqno,
          potato,
          telno,
        } = res
        this.setState({
          msgadmin,
          wechat,
          qqno,
          potato,
          telno,
        })
      }
    })
  }

  onLeftClick = () => this.props.history.go(-1);

  sendMsg = () => {
    let what = prompt(": ", "");
    let who = this.state.msgadmin
    if (what !== null && who !== '') {
      if (window.confirm(`${who}${what}?`) === true) {
        window.parent.location.href = `mailto:${who}?subject=${what}`;
      }
    }
  }

  copyWechat = () => {
    copy(this.state.wechat);
    Toast.success('', 1, false)
  }

  copyQQ = () => {
    copy(this.state.qqno);
    Toast.success('', 1, false)
  }

  copyPotato = () => {
    copy(this.state.potato);
    Toast.success('', 1, false)
  }

  render() {
    const { getFieldProps } = this.props.form;
    const NavBarProps = {
      edit: false,
      title: '',
      onLeftClick: this.onLeftClick,
    }

    return (
      <div className='body'>
        <PubicNavBar {...NavBarProps} />
        <div style={{ padding: '0.2rem' }}>
          <TextareaItem
            {...getFieldProps('userFeedBack', {
              //initialValue: '', //
            })}
            placeholder='...'
            rows={8}
            count={500}
            style={{ fontSize: '.24rem' }}
          />
          <p className='feedbackimg'>3</p>
          <ImagePicke
            userFeedBack={this.props.form.getFieldProps('userFeedBack').value}
            form={this.props.form}
          />
          <div style={{ marginTop: '.2rem', marginBottom: '.9rem' }}>
            <p className='feedbackimg'></p>
            <div className='feedBackStyles'>
              <p className='feedBackText'>: {this.state.msgadmin}</p>
              <Button onClick={this.sendMsg} className='feedBackBtn'></Button>
            </div>
            <div className='feedBackStyles'>
              <p className='feedBackText'>: {this.state.wechat}</p>
              <Button onClick={this.copyWechat} className='feedBackBtn'></Button>
            </div>
            <div className='feedBackStyles'>
              <p className='feedBackText'>QQ: {this.state.qqno}</p>
              <Button onClick={this.copyQQ} className='feedBackBtn'>QQ</Button>
            </div>
            <div className='feedBackStyles'>
              <p className='feedBackText'>Potato{this.state.potato}</p>
              <Button onClick={this.copyPotato} className='feedBackBtn'></Button>
            </div>
            <div className='feedBackStyles'>
              <p className='feedBackText'>: {this.state.telno}</p>
              <Button onClick={() => window.location.href = `tel:${this.state.telno}`} className='feedBackBtn'>
                
              </Button>
            </div>
          </div>
        </div>
      </div >
    )
  }
}

export default UserFeedBack
